import { Addon } from "@wealthica/wealthica.js";

export default {
    state: {
      addon: null,
      options: {},
      institutions: null,
      assets: null,
      loading: true,
      selectedCurrency: null,
      savedTransactions: [],
    },
    getters: {
      addon: state => state.addon,
      institutions: state => state.institutions,
      assets: state=> state.assets,
      language: () => "en",
      loading: state => state.loading,
      selectedCurrency: state => state.selectedCurrency,
      savedTransactions: state => state.savedTransactions,
    },
    mutations: {
      SET_ADDON(state, data) {
        state.addon = data;
      },
      SET_LOADING(state, loading) {
        state.loading = loading;
      },
      STORE_INSTITUTIONS(state, institutions){
          state.institutions = institutions;
      },
      STORE_ASSETS(state, assets){
        state.assets = assets;
      },
      SET_SELECTED_CURRENCY(state, currency){
        state.selectedCurrency = currency;
      },
      SET_SAVED_TRANSACTIONS(state, savedTransactions){
        state.savedTransactions = savedTransactions;
      }
    },
    actions: {
      initAddon({ commit, dispatch }) {
        // This is only required in the add-on release preparation process.
        const addonConfiguration =
          process.env.VUE_APP_DEVELOPER_ADDON === "true"
            ? {}
            : { id: "wealthica/wealthica-csv-import-addon" };
  
        const addon = new Addon(addonConfiguration);
        commit("SET_ADDON", addon);
  
        addon
          .on("init", options => dispatch("updateData", options))
          .on("update", options => dispatch("updateData", options));
      },
      async updateData({  commit, dispatch }) {
        commit("SET_LOADING", true);
        await dispatch("fetchInstitutionsAndAssets");
        await dispatch("fetchCurrencies");
        commit("SET_LOADING", false);
      },

      async fetchInstitutionsAndAssets({ commit, getters }) {
        const { addon } = getters;
        const institutions = await addon.api.getInstitutions();
        const assets = await addon.api.getAssets();
        commit("STORE_INSTITUTIONS", institutions);
        commit("STORE_ASSETS", assets);
      },
      async fetchCurrencies({ commit, getters }) {
        const { addon } = getters;
        const currencies = await addon.api.getCurrencies();
        const preferred = currencies.find(currency => currency.preferred);
        commit("SET_SELECTED_CURRENCY", preferred);
      },

      async saveTransactions({commit, dispatch}, payload){
        //commit("SET_LOADING", true);
        const importTo =  payload.importTo;
        let accountInfo = {}
        if(importTo == 'institution'){
          accountInfo = {institution: payload.institutionId, investment: payload.investmentId };
        }else {
          accountInfo = { asset: payload.assetId };
        }
        const importData = payload.importData;
        const savedTransactions  = await Promise.all(importData.map(async (transaction) => {
          transaction = {...transaction, ...accountInfo};
          return await dispatch('postTransaction', transaction)
        }));
        //commit("SET_LOADING", false);
        commit("SET_SAVED_TRANSACTIONS", savedTransactions);
      },

      async postTransaction({getters}, payload){
        const { addon } = getters;
        return new Promise((resolve)=>{
          const requestObject = {
            method: 'POST',
            endpoint: 'transactions',
            body: payload,
          }
          addon.request(requestObject).then(function (response) {
            resolve({error: null, response});
          }).catch(function (err) {
            resolve({error: err, response: null});
          });
        })
      }
      
      
    }
  };