						4. Identify columns to import ...
					</div>
				</v-col>
				<v-col cols="24" sm="12">
					<div class="account-currency">Account currency ...</div>
					Selected account currenty is
					<span class="selected-currency">{{
						selectedCurrency._id
					}}</span>
					all transactions amount will be importes as
					<span class="selected-currency">{{
						selectedCurrency._id
					}}</span>
				</v-col>
				<v-col cols="12" sm="6">
					<v-row>
						<v-col cols="12" sm="6">
							<div ref="topelement"></div>
