										headerWithDataType.dataType ==
										'ignored',
								}"
							>
								<option value="date">Date</option>
								<option value="currency_amount">Amount</option>
								<option value="type">Type</option>
								<option value="asset">Asset</option>
								<option value="category">Category</option>
								<option value="description">Description</option>
								<option value="fee">Fee</option>
								<option value="institution">Institution</option>
								<option value="investment">Investment</option>
								<option value="note">Note</option>
								<option value="quantity">Quantity</option>
								<option value="realized_gain">Gain</option>
								<option value="security">Security</option>
								<option value="settlement_date"
									>Settlement Date</option
								>
								<option value="symbol">Symbol</option>
								<option value="ignored">Ignored</option>
							</select>
						</v-col>
					</div>
				</v-col>
				<v-col cols="12" sm="6">
					<div class="guidelines">
						<label class="as">Guidelines</label>
						<div>
							Make sure you map at least the mandatory columns to
							initiate the process. Date, Type and Amount are
							required. You can pick multiple fields as
							Description and the data will be joined into the
							description fields.
						</div>
						<div class="mandatory-sec">
							<div class="mandatory">Mandatory</div>
							<div>
								Date: Is the transaction date.<br />
								Type: Is the transaction type which can be one
								of deposit, withdrawal, transfer, switch,
								interest, dividend, distribution, roc, reinvest,
								tax, fee, sell, buy, income, mtm or unknown.<br />
								Amount: Is the total value of the transaction
								net of fees.<br />
								Symbol: The stock symbol, required for roc,
								reinvest, sell and buy.
							</div>
						</div>
						<div class="mandatory-sec">
							<div class="mandatory">Optional</div>
							<div>
								Description: The description of the
								transaction.<br />
								Fee: The commission or transaction fee.<br />
								Quantity: Usually the number of stocks bought or
								sold.<br />
								Settlement Date: The date the transaction was
								settled.<br />
								Category: Usually used to categorize an
								expense.<br />
							</div>
						</div>
						<div class="mandatory-sec">
							<div class="mandatory">Ignored</div>
							<div>
								The data from columns marked as Ignored won’t be
								imported unless you map the data with one of the
								mandatory or optional fields. If you would like
								those specific columns to be imported you can
								add the data to the Description field.
							</div>
						</div>
						<div class="mandatory-sec">
							<div class="mandatory">Type >  Unknown</div>
							<div>
								Transactions of type unknown will be imported
								into Wealtica. Unknown type must be used when
								the transaction can’t be mapped to the available
								Wealthica transaction types.
							</div>
						</div>
					</div>
				</v-col>
			</v-row>

			<v-row v-if="headersWithDataType.length">
				<v-col cols="24" sm="12">
					<div class="row-heading">
						5. Review data that will be imported ...
					</div>
					<!-- <v-avatar class="mr-4">
          <v-img
            :src="require(`@/assets/img/ignored.png`)"
          />
        </v-avatar> -->
				</v-col>
				<v-col cols="24" sm="12" align="center" justify="center">
					<v-btn depressed color="primary" @click="uploadCSV"
						>Import</v-btn
					>
				</v-col>
				<v-col cols="24" sm="12">
					<v-layout column class="review-section">
						<v-flex>
							<v-simple-table>
								<template v-slot:default>
									<thead>
										<tr>
											<th
												v-for="headerDataType in acceptedHeaders"
												:key="headerDataType.header"
												class="text-left"
											>
												{{ headerDataType.header }} as
												{{ headerDataType.dataType }}
											</th>
										</tr>
									</thead>
									<tbody>
										<tr
											v-for="(row,
											index) in dataBeingImported"
											:key="index"
										>
											<td
												v-for="(key, value) in row"
												:key="value"
											>
												{{ key }}
											</td>
										</tr>
									</tbody>
								</template>
							</v-simple-table>
