		</form>
		<div class="text-center">
			<v-dialog v-model="dialog" persistent width="300">
				<v-card color="primary" dark>
					<v-card-text>
						Saving your transactions! Please wait!
						<v-progress-linear
							indeterminate
							color="white"
							class="mb-0"
						></v-progress-linear>
					</v-card-text>
				</v-card>
			</v-dialog>
		</div>
	</div>
</template>
<script>
import {
	isKeyIgnored,
	findDataType,
	parseCSV,
	getSelectedDataTypeAndValue,
} from "../utils/util";
import { mapGetters, mapActions } from "vuex";
import { required } from "vuelidate/lib/validators";

import ASSET_TYPES from "../utils/assets";

export default {
	data() {
		return {
			importTo: "institution",
			account: null,
			institutionId: "",
			investmentId: "",
			assetId: "",
			file: null,
			headers: [],
			headersWithDataType: [],
			dataArr: [],
			dialog: false,
		};
	},
	validations: {
		institutionId: {
			required,
		},
		investmentId: {
			required,
		},
		assetId: {
			required,
		},
	},
	computed: {
		...mapGetters([
			"institutions",
			"assets",
			"selectedCurrency",
			"savedTransactions",
		]),
		dataBeingImported() {
			let parsedData = this.dataArr.map((data) => {
				let newObj = {};
				Object.entries(data).forEach((entry) => {
					const [key, value] = entry;
					if (!isKeyIgnored(this.headersWithDataType, key)) {
						newObj[key] = value;
					}
				});
				return newObj;
			});
			return parsedData;
		},
		importData() {
			let parsedData = this.dataBeingImported.map((data) => {
				let newObj = {};
				Object.entries(data).forEach((entry) => {
					const [key, value] = entry;
					const { dataType, newValue } = getSelectedDataTypeAndValue(
						this.acceptedHeaders,
						key,
						value
					);
					newObj[dataType] = newValue;
				});
				if (newObj.type == "withdrawal") {
					newObj.currency_amount = -newObj.currency_amount;
				}
				return newObj;
			});
			return parsedData;
		},
		investments() {
			if (this.institutionId) {
				return this.institutions.filter(
					(institution) => institution.id == this.institutionId
				)[0].investments;
			} else {
				return [];
			}
		},
		acceptedHeaders() {
			return this.headersWithDataType.filter(
				(item) => item.dataType != "ignored"
			);
		},
	},
	methods: {
		...mapActions(["saveTransactions"]),
		async handleFileUpload(event) {
			event.preventDefault();
			const files = event.target.files || event.dataTransfer.files;
			const data = await this.getFileData(files[0]);
			this.dataArr = parseCSV(data);
			const headers = this.dataArr[0];
			const headersWithDataType = [];
			Object.entries(headers).map(([key, value]) => {
				const headerWithDataType = findDataType(key, value);
				headersWithDataType.push(headerWithDataType);
			});
			this.headersWithDataType = headersWithDataType;
		},
		async getFileData(file) {
			return new Promise((resolve, reject) => {
				const reader = new FileReader();
				reader.readAsDataURL(file);
				reader.onload = () =>
					resolve(atob(reader.result.split(",")[1]));
				reader.onerror = (error) => reject(error);
			});
		},
		async changeDataType(event, header) {
			event.preventDefault();
			const dataType = event.target.value;
			this.headersWithDataType = this.headersWithDataType.map(
				(headerDataType) => {
					if (headerDataType.header == header) {
						return { header: header, dataType: dataType };
					} else {
						return { ...headerDataType };
					}
				}
			);
		},
		async uploadCSV() {
			//alert(JSON.stringify(this.acceptedHeaders)+JSON.stringify(this.importData))
			this.$v.$touch();
			if (this.$v.$invalid) {
				console.log(JSON.stringify(this.$v));
				if (this.importTo == "institution") {
					if (this.$v.institutionId.$error) {
						const element = this.$refs["institutionId"];
						element.scrollIntoView({ behavior: "smooth" });
						return;
					} else if (this.$v.investmentId.$error) {
						const element = this.$refs["investmentId"];
						element.scrollIntoView({ behavior: "smooth" });
						return;
					}
				} else {
					if (this.$v.assetId.$error) {
						const element = this.$refs["assetId"];
						element.scrollIntoView({ behavior: "smooth" });
						return;
					}
				}
			}

			const element = this.$refs["topelement"];
			element.scrollIntoView({ behavior: "smooth" });
			//alert(JSON.stringify(this.acceptedHeaders));

			//validations

			const selectedHeaders = this.acceptedHeaders.filter((element) => {
				return (
					element.dataType == "currency_amount" ||
					element.dataType == "date" ||
					element.dataType == "type" ||
					element.dataType == "description"
				);
			});

			if (selectedHeaders.length < 4) {
				alert(
					"Types currency_amount, date, type , description is required"
				);
				return;
			}

			this.dialog = true;

			const payload = {
				importTo: this.importTo,
				institutionId: this.institutionId,
				investmentId: this.investmentId,
				assetId: this.assetId,
				importData: this.importData,
			};
			await this.saveTransactions(payload);
			if (this.savedTransactions && this.savedTransactions.length) {
				//console.log("+++++++++++++++++++++++++++++");
				//console.log(JSON.stringify(this.savedTransactions));
				setTimeout(() => {
					this.dialog = false;
					let redirectURL = "";
					if (this.importTo == "institution") {
						redirectURL = `institutions=${this.institutionId}&accounts=${this.investmentId}`;
					} else {
						redirectURL = `assets=${this.assetId}`;
					}
					redirectURL = `https://app.wealthica.com/transactions?peek=true&${redirectURL}%3Amargin%3Ausd`;
					console.log(`redirectURL :  ${redirectURL}`);
					//alert(redirectURL);
					window.location.href = redirectURL;
				}, 500);
			}
		},
		getAssetIcon(asset){
			if(asset.image) return asset.image;
			return this.iconClassName(asset.type);
		},
		iconClassName(type) {
			console.log('ASSET_TYPES '+ASSET_TYPES);
			let icon;
			Object.entries(ASSET_TYPES).forEach((entry) => {
				const [assetType] = entry;
				if (assetType === type) {
					icon = ASSET_TYPES[assetType].icon;
				} else if (
					ASSET_TYPES[assetType].sub_types &&
					ASSET_TYPES[assetType].sub_types[type]
				) {
					icon =
						ASSET_TYPES[assetType].sub_types[type].icon ||
						ASSET_TYPES[assetType].icon;
				}
			});
			return icon;
		},
	},
};
</script>

